
.storycontainer{
display: flex;
justify-content: space-around;
}

.storycontainer div{
    position: relative;
    width: 65px;
    height: 65px;
    background-color: rgba(255, 255, 255, 0.603);
    border: 3px var(--prim-color-opac) solid;
    border-radius: 50%;
    cursor: pointer;
    transition: 500ms;
}
.storycontainer div:hover{
    background-color: rgb(255, 255, 255);
    border: 3px #F54B64 solid;
    border-radius: 50%;
    transform: scale(1.3);
}


