:root{  
    --prim-color: #F54B64;
    --prim-color-opac: #f54b64b1;
    --second-color: #282c34;
    --white: #FFFFFF;
    --error-color: #BF263C;
    --dark-grey: #4E586E;
  }

.timeline{
    width: 100%;
    color: white;
    display: flex;
    flex-direction: column;
    align-content: flex-start;
}

.tweet{
    border-bottom: rgba(255, 255, 255, 0.247) 1px solid;
    padding: 15px;
    text-align: left;
    min-height: 120px;
}

.acount{
    display: flex;
    gap: 8px;
    align-items: center;
}

.acount img {
    width: 45px;
    height: 45px;
    background-color: rgba(255, 255, 255, 0.603);
    border: 3px var(--prim-color) solid;
    border-radius: 50%;
    cursor: pointer;
}

    /*poster name*/

.acount span:first-of-type{
    font-size: 18px;
}
    /*user and date*/

.acount span:nth-of-type(2), span:nth-of-type(3){
    color: gray;
}

.acount span:nth-of-type(2)::before{
    content: "@";
}

.tweet-post{
    background-color: var(--white);
    border-radius: 8px;
    padding: 15px;
    margin-top: 20px;
    position: relative;
    color: var(--second-color);
}

    /*post box arrow*/

.tweet-post::before{
    content: '';
    width: 0;
    height: 0;
    border: 20px transparent solid;
    border-bottom: honeydew 20px solid;
    position: absolute;
    top: -35px;
    left: 6px;
}